/*
** Compoent is used for show event list in container-1
**/

import React from 'react';
import { inject, observer } from 'mobx-react';

import { Event } from '../components/';

@inject('store')
@observer
export default class EventList extends React.Component {

  constructor() {
    super();
  }

  eventlist (events) {
    if (events.size == 0) {return <h3>No data found</h3>;}
    let items = events.values().sort().reverse();
    return items.map((el) => <Event key={el.id} event_id={el.id} />);
  }

  render() {
    let {appStatus, filterEvents, events} = this.props.store;
    let event_list = appStatus.isSearch == true ? filterEvents : events;
    let eventEle = this.eventlist(event_list);

    return (
      <div className="top-h-panel panel-h-left col-md-4">
        {eventEle}
      </div>
    );
  } 
}



