/*
** Top navigation component
**/

import React from 'react';
import { inject, observer } from 'mobx-react';
import { Link } from 'react-router';

@inject('store')
@observer
export default class Header extends React.Component{
  
  constructor() {
    super();
  }

  toggleLogout(e) {
    this.props.store.logout();
  }
  
  render() {
    let { user } = this.props.store;
    return(
      <nav className="navbar navbar-inverse" role="navigation">
        <div className="container">
          <div className="row">
            <div className="navbar-header">
              <div className="navbar-brand navbar-brand-centered"><figure className="logo"><img src="./../hypy_logo.png" className="img-responsive" /></figure></div>
            </div>
            <div className="collapse navbar-collapse" id="navbar-brand-centered">
              <ul className="nav navbar-nav">
                <li><a className="header-top">HYPY - ADMIN <span className="ver-cap">{user.version}</span></a></li>
              </ul>
              <ul className="nav navbar-nav navbar-right">
                <li className="dropdown">
                  <a href="#" className="dropdown-toggle" data-toggle="dropdown">
                    {user.profile_image ? 
                      <img src={user.profile_image} className="img-responsive header_user_pic" />
                      :
                      <img src="./../user_profile_image.png" className="img-responsive header_user_pic" />
                    }
                   {user.first_name} {user.last_name} <span className="caret"></span></a>
                  <ul className="dropdown-menu" role="menu">
                    <li><a href={'admin/user/' + user.id}>My Profile</a></li>
                    <li><a href={'settings'}>App Settings</a></li>
                    <li><a href={'admin/admin_users'}>Manage Admin</a></li>
                    <li><a href={'admin/manage_users'}>Manage Users</a></li>
                    <li><a onClick={this.toggleLogout.bind(this)}>Logout</a></li>
                  </ul>
                </li>           
              </ul>
            </div>
          </div>
        </div>
      </nav>
    );
  }
}


