/*
 * Entry page of application
 */

import React from 'react';
import ReactDOM from 'react-dom';
import { Router, Route, IndexRoute, hashHistory } from "react-router";
import { Provider } from 'mobx-react';

import { Layout, FrontPage, DetailsPage } from './pages';
import { store } from './stores';

const contentContainer = document.getElementById('the-container');

class App extends React.Component {
 
  constructor() {
    super();
  }
  
  removeMenus(e) {
    
    //Don't call prevent default or you'll ruin their normal behavior!
    [].forEach.call( document.getElementsByTagName('body'), (el) => {
      el.classList.remove('show-nav-menu');
    });
    document.getElementById('the-container').classList.remove('show-nav-menu');
  }
  
  render() {
    return (
      <Provider store={store}>
        <div id="the-app-content" onClick={this.removeMenus.bind(this)}>
          <div id="content">
            <Router history={hashHistory}>
              <Route path="/" component={Layout}>
                <IndexRoute component={FrontPage}></IndexRoute>
                <Route path="details/:gallery" name="details" component={DetailsPage}></Route>
              </Route>
            </Router>
          </div>
        </div>
      </Provider>
    );
  }  
}

ReactDOM.render( <App />, contentContainer );  



