/*
** This file is used for to show message for create/ update event
*/

import React from 'react';
import { inject, observer } from 'mobx-react';

@inject('store')
@observer
export default class CreateEvent extends React.Component {

  constructor() {
    super();
  }

  render() {
    let { appStatus } = this.props.store;
    return (
      <div className="col-md-4 event-count">
        <h3>{appStatus.editEvent == true? 'Update Eevnt' : 'Create Event'}</h3>
      </div>
    );
  } 
}



