/*
** This component is used for to show the event selected event content details in container 3
*/
import React from 'react';
import { inject, observer } from 'mobx-react';

import CreateComment from './CreateComment';
import CommentsList from './CommentsList';
import EditComment from './EditComment';
import VideoFile from './VideoFile';
import EventContainerHead from './EventContainerHead';
import ImageShow from './ImageShow';
import moment from 'moment';

@inject('store')
@observer
export default class EventContentSelected extends React.Component {
 
  constructor() {
    super();
    this.state = {isChecked: true}
  }

  handleCheckboxChange (event) {
    this.setState({isChecked: event.target.checked});
  }

  getEventContentBlock (file_url, thumb_url) {
    let mime = file_url ? file_url.split('.').pop() : '';
    if ((mime == 'mp4' ||mime == 'mov' || mime == 'avi' || mime == '3gp' || mime == 'flv' || mime == 'wmv')) {
      return <VideoFile file_url={file_url} thumb_url={thumb_url} />
    } else {
      let img_url = file_url ? file_url : 'no-image.jpg';
      return <ImageShow img_src={img_url} thumb_url={thumb_url} />
    }
  }

  render() {
    let { appStatus, events } = this.props.store;
    let event = events.get(appStatus.event_id);
    let photo = event.photos.get(appStatus.photo_id);
    let blockName = 'camera-block ';
    if(photo.flagged == true) {
      blockName = blockName + 'bg-red';
    } else if(photo.moderated == true) {
      blockName = blockName + 'bg-green';
    } else {
      blockName = blockName + 'bg-yellow';
    }
    let mime = '';
    if (photo.file_url)  {
      mime = photo.file_url.split('.').pop();
    }
    let event_cont_ele = this.getEventContentBlock(photo.file_url, photo.thumb_url);
    let created_at = moment(photo.created_at.slice(0, -4), "DD-MM-YYYY  hh:mm:ss a").format('LLL');
    let css_styles = {display: 'block'};
    if (this.state.isChecked == false) {
      css_styles = {display: 'none'};
    }
    return (
      <div className="top-h-panel panel-h-right col-md-4">
        <div className="sub-title">Content Details</div>
        <div className={blockName}>
          <EventContainerHead photo_id={photo.id} photo={photo} uncheck="flase" />
          {event_cont_ele}
          <div className="desciption">
            <div className="block"><div className="pull-left"><span className="user_pic">
              {photo.user_profile_image ? 
                  <img src={photo.user_profile_image} className="img-responsive" />
                  :
                  <img src="./../user_profile_image.png" className="img-responsive" />
              }
              </span> <span><b>{photo.user_name}</b></span></div><div className="pull-right"><span className=" bluecolor fa fa-thumbs-up"><span className="font_small">{photo.count_likes}</span></span> <span className="font_small fa fa-comments green"><span className="font_small">{photo.count_comments}</span></span>
            </div>
            <div className="clearfix"></div>
          </div>
          <div className="block comment-box"><span><i class="fa fa-map-marker" aria-hidden="true"></i> </span><span>{photo.location ? photo.location : 'Not Available'}</span>
            <div className="clearfix"></div>
          </div>
          <div className="block comment-box1"><span></span><span>{photo.description}</span>
            <div className="clearfix"></div>
          </div>
          <p className=" pull-right comment_date"><span className="font_small">{created_at}</span></p>
          <div className="clearfix"></div>
          <div className="block"><span className="padding10">Show Comments:</span>
              <span>
                <input type="checkbox" onChange={this.handleCheckboxChange.bind(this)} checked={this.state.isChecked} />
              </span>
            </div>
          </div>
          <CommentsList  photo_id={appStatus.photo_id} comments={photo.comments.values()} css_styles={css_styles} /> 
        </div>
        {appStatus.editComment == true ? <EditComment comment_id={appStatus.comment_id} comment={appStatus.comment} /> : <CreateComment />}
      </div>
    );
  }
}


