/*
** This file is used for to show the video player
**/

import React from 'react';
import { inject, observer } from 'mobx-react';
import { Player } from 'video-react';

@inject('store')
@observer
export default class VideoFile extends React.Component {

  static propTypes = {
    file_url: React.PropTypes.string.isRequired
  }

  render() {
    let file_url = this.props.file_url;
    let thumb_url = this.props.thumb_url;

    return (
      <Player
        playsInline
        poster={thumb_url}
        src={file_url}
      />
    );
  }
}



