/*
** This file is used to create proto type of events data
**/

import { action, computed, observable } from 'mobx';
import Photos from './Photos';

export default class {
  id = null;
  @observable count_active_users = 0;
  @observable count_photos = 0;
  @observable description = '';
  @observable hero_image = '';
  @observable location = null;
  @observable name = '';
  @observable owner_type = null;
  @observable path = '';
  @observable hero_image_file = '';
  photos = observable.map({});
  @observable user_id = null;

  @observable bypass_moderation = false;
  @observable status_color = '';
  @observable created_at = '';

  static jsonToArray(json) {
    let event = {};
    json.map(item => {
      event[item.id] = this.constructFromJSON(item);
    });
    return event;
  }

  static constructFromJSON(json) {
    return new this(
      json.id,
      json.count_active_users,
      json.count_photos,
      json.description,
      json.hero_image,
      json.location,
      json.name,
      json.owner_type,
      json.path,
      json.hero_image_file,
      json.photos,
      json.user_id,
      json.bypass_moderation,
      json.status_color,
      json.created_at
    );
  }    

  constructor(
    id,
    count_active_users,
    count_photos,
    description,
    hero_image,
    location,
    name,
    owner_type,
    path,
    hero_image_file,
    photos,
    user_id,
    bypass_moderation,
    status_color,
    created_at
    ) {
    this.id = id;
    this.count_active_users = count_active_users;
    this.count_photos = count_photos;
    this.description = description;
    this.hero_image = hero_image;
    this.location = location;
    this.name = name;
    this.owner_type = owner_type;
    this.path = path;
    this.hero_image_file = hero_image_file;
    count_photos ? this.photos.replace(Photos.jsonToArray(photos)) : this.photos;
    this.user_id = user_id;
    this.bypass_moderation = bypass_moderation;
    this.status_color = status_color;
    this.created_at = created_at;
  }

  update(entries) {
    for (let key in entries) {
      this[key] = entries[key];
    }
  }
}


