/*
** This component is used for to create event of selected event content
*/

import React from 'react';
import { inject, observer } from 'mobx-react';

@inject('store')
@observer
export default class CreateComment extends React.Component {
 
  constructor() {
    super();
    this.state = {comment: ''}
  }

  createNewComment (event) {
    let comment = this.state.comment.trim();
    if (!comment) {
      alert('Please enter a comment');
    } else {
      this.props.store.createComment(comment);
    }
    this.setState({comment: ''});
    event.preventDefault();
  }

  handleCommentChange (event) {
    this.setState({comment: event.target.value});
    event.preventDefault();
  }

  render() {
    let { appStatus, events } = this.props.store;
    let event = events.get(appStatus.event_id);
    let photo = event.photos.get(appStatus.photo_id);

    return (
      <form onSubmit={this.createNewComment.bind(this)}>
        <div className="form-group">
          <label>Add Comment</label>
          <textarea
            className="form-control" 
            placeholder="Add a new comment"
            onChange={this.handleCommentChange.bind(this)}
            value={this.state.comment}
          />
        </div>
        <button type="submit" className="btn btn-primary btn-100">Save</button>
      </form>
    );
  }
}


