/*
** This component is used to search events in event list
**/

import React from 'react';
import { inject, observer } from 'mobx-react';

@inject('store')
@observer
export default class EventSearch extends React.Component {

  constructor() {
    super();
    this.state = {search: ''}
  }

  handleSearch (event) {
    this.props.store.filterByEvent();
    this.props.store.appStatus.isSearch = true;
    event.preventDefault();
  }

  handleChange (event) {
    this.setState({search:event.target.value});
    this.props.store.appStatus.searchEle = event.target.value;
    if (event.target.value == '') {
      this.props.store.appStatus.isSearch = false;
      this.props.store.filterByEvent();
    }
    event.preventDefault();
  }

  render() {
    this.state.search = this.props.store.appStatus.searchEle;

    return (
      <div className="col-md-4 event-count">
        <div className="col-md-12">
        <form onSubmit={this.handleSearch.bind(this)}>
          <div className="input-group">
            <input
              type="text"
              className="form-control"
              placeholder="Search for..."
              onChange={this.handleChange.bind(this)}
              value={this.state.search}
            />
            <span className="input-group-btn">
              <input type="submit" className="btn btn-primary" value="Go" />
            </span>
          </div>
          </form>
        </div>
      </div>
    );
  } 
}



