/*
** This component is used to show image
**/

import React from 'react';
import { inject, observer } from 'mobx-react';
import ImageLoader from 'react-imageloader';

@inject('store')
@observer
export default class ImageShow extends React.Component {

  constructor() {
    super();
  }
  preloader() {
    return <img src="/image-loader.gif" />;
  }

  render() {
    let image_url = this.props.img_src;
    return (
      <ImageLoader src={image_url} wrapper={React.DOM.figure} preloader={this.preloader} >
        <img src="/no-image.jpg" />
      </ImageLoader>
    );
  }
}


