/*
** This component is used for to show the event content lists
*/

import React from 'react';
import { inject, observer } from 'mobx-react';
import { EventContent } from './../../components/';

@inject('store')
@observer
export default class EventContentListing extends React.Component {

  eventCont (ev_con) {
    if (ev_con.size == 0) {return <h3>No data found</h3>;}
    let items = ev_con.values().sort().reverse();
    return items.map((el) => <EventContent key={el.id} photo_id={el.id} />);
  }
  
  render() {
    let {appStatus, filterEventContent, events} = this.props.store;
    let ev_cont = appStatus.isSearchContent == true ? filterEventContent : events.get(appStatus.event_id).photos;
    let eventEle = this.eventCont(ev_cont);

    return (
      <div className="top-h-panel panel-h-middle col-md-4">
        {eventEle}
      </div>
    );
  } 
}



