/*
 * Global storage file is used to store, update, delete, retrieve data. And all global methods are defined here
 */
import { action, observable, toJS } from 'mobx';
import { hashHistory } from 'react-router';
import axios from 'axios';
import services from './../services';
import Event from './Event';
import Photos from './Photos';
import Comments from './Comments';

class Store {

  // Users details
  user = observable.map({});

  // EVents details
  events = observable.map({});
  filterEvents = observable.map({});
  filterEventContent = observable.map({});
  // Default applicaton status
  appStatus = observable({
    searchEle: '',
    searchContent: '',
    isSearch: false,
    isSearchContent: false,
    isCreateEvent: true,
    isCreateEventContent: false,
    preLoader: true,
    eventSearchbar: true,
    event_id: '',
    photo_id: '',
    temp_photo_id: '',
    processing: false,
    loadComment: false,
    editEvent: false,
    editComment: false,
    editCommLoading: false,
    comment: observable.map({}),
    comment_id: ''
  });

  @observable tempEvent = null;
  @observable tempEventContent = null;
  @observable comment = null;
  @observable autoload;
  geo_loc = '';
  preLoadUser(userID) {
    this.appStatus.preLoader = true;
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition((pos) => {
        this.geo_loc = pos.coords.latitude + "," + pos.coords.longitude;
      });
    }
    if (userID === null || userID === undefined) {
      userID = "me";
    }
    services.getAppVersion().then(appRes => {
      services.gerUser(userID).then(userRes => {
        if (userRes.error == true) {return;}
        userRes.content.version = appRes.content.version;
        this.user = userRes.content;
        services.getEvents().then(eventRes => {
          if (eventRes.error == true) {return;}
          this.events.replace(Event.jsonToArray(eventRes.content))
          this.appStatus.preLoader = false;
        });
      });
    });
  }
  showPosition (position) {
    var geo_loc = position.coords.latitude + " " + position.coords.longitude;
    return geo_loc;
  }
  setDefault () {
    this.appStatus.isCreateEvent = false;
    this.appStatus.preLoader = false;
    this.appStatus.eventSearchbar = false;
    this.appStatus.event_id = '';
    this.appStatus.event = '';
  }

  logout () {
    services.logout().then(repData => {
      window.location.reload('/login');
    });
  }

  getSelectedEvent () {
    return this.appStatus.event_id;
  }

  setSelectedEvent (event_id) {
    this.appStatus.event_id = event_id;
  }


  filterByEvent () {
    this.filterEvents.clear();
    let search = this.appStatus.searchEle.toLowerCase();
    this.events.values().map(item => {
      if (item.name.toLowerCase().search(search) !== -1 || item.description.toLowerCase().search(search) !== -1) {
        this.filterEvents.set(item.id, this.events.get(item.id));
      }
    });
  }

  filterByEventContent () {
    this.filterEventContent.clear();
    let search = this.appStatus.searchContent.toLowerCase();
    let event_id = this.getSelectedEvent();
    let event = this.events.get(event_id);
    services.getFilteedEventContent(event_id, search).then(filtRes => {
      if (filtRes.error == true) {return;}
      event.photos.values().map(item => {
        if ((filtRes.content.indexOf(item.id) != -1)) {
          this.filterEventContent.set(item.id, event.photos.get(item.id));
        }
      });
    });
  }

  @action
  flagEventContent (flag, photo_id, temp_photo) {
    let photos = this.events.get(this.appStatus.event_id).photos;
    if (flag === false) {
      services.deleteFlagEventContent(photo_id).then(photosRes => {
        if (photosRes.error == true) {alert('Fail to delete flag event content');return;}
      });
    } else {
      services.flagEventContent(photo_id, {}).then(photosRes => {
        if (photosRes.error == true) {alert('Fail to flag event content');return;}
      });
    }
    photos.set(photo_id, null);
    photos.set(photo_id, temp_photo);
  }

  @action 
  deleteEventContent (photo_id){
    let event_id = this.appStatus.event_id;
    let event = this.events.get(this.appStatus.event_id);
    services.deleteEventContent(photo_id).then(photosRes => {
      if (photosRes.error == true) {alert('unable to delete event content');return;}
      event.photos.delete(photo_id);
      alert('Event content deleted successfully!');
      services.getEventById(event_id).then(eveRes => {
        let temp_event = Event.constructFromJSON(eveRes.content);        
        temp_event.photos = event.photos;
        this.events.set(this.appStatus.event_id, temp_event);
        this.appStatus.isCreateEventContent = true;
        this.appStatus.processing = false;
        this.appStatus.photo_id = '';
      });
    });
  }

  @action 
  getPhotos(event_id) {
    services.getPhotos(event_id).then(photosRes => {
      if (photosRes.error == true) {return;}
      let event = this.events.get(event_id);
      let comments;
      let photos = Photos.jsonToArray(photosRes.content);
      photosRes.content.map((item, index) => {
        if (this.appStatus.event_id && this.appStatus.photo_id) {
          comments = event.photos.get(this.appStatus.photo_id).comments;
          photosRes.content[index].comments = comments.values();
        }
      });
      event.photos.replace(Photos.jsonToArray(photosRes.content));

    });
  }

  @action
  getPhotosComments (event_id, photo_id) {
    services.getPhotoComments(event_id).then(photosRes => {
      if (photosRes.error == true) {return;}
      let filtComm = photosRes.content.filter((elem) => {
        return elem.photo_id == photo_id;
      });
      let photo = this.events.get(event_id).photos.get(photo_id);
      photo.comments.replace(Comments.jsonToArray(filtComm));
      this.appStatus.loadComment = false;
    });
  }

  @action
  UpdateComment (comm) {
    let event_id = this.appStatus.event_id;
    let photo_id = this.appStatus.photo_id;
    let user_name = this.user.first_name + " " + this.user.last_name;
    const formData = new FormData();
    formData.append('text', comm.text);
    formData.append('user_name', user_name);
    services.updateComment(comm.id, formData).then(commRes => {
      if (commRes.error == true) {
        this.appStatus.editComment = false;
        alert('Unable to update a comment');
        return;
      }
      let comments = this.events.get(event_id).photos.get(photo_id).comments;
      comments.set(comm.id, commRes.content);
      this.appStatus.editComment = false;
      alert('Comment updated successfully!');
    });
  }

  @action
  deleteComment (comm_id) {
    let event_id = this.appStatus.event_id
    let photo_id = this.appStatus.photo_id
    let comments = this.events.get(event_id).photos.get(photo_id).comments;
    services.deleteComment(comm_id).then(commRes => {
      if (commRes.error == true) {alert('Unable to delete a comment');return;}
      comments.delete(comm_id);
      let photo = this.events.get(event_id).photos.get(photo_id);
      photo.count_comments--;
      alert('Comment deleted successfully!');
    });
  }

  @action 
  createComment(comment) {
    const formData = new FormData();
    let event_id = this.appStatus.event_id;
    let photo_id = this.appStatus.photo_id;
    let user_name = this.user.first_name + " " + this.user.last_name;
    formData.append('text', comment);
    formData.append('user_name', user_name);
    services.postComment(event_id, photo_id, formData).then(commRes => {
      if (commRes.error == true) {alert('Unable to add a comment');return;}
      let photo = this.events.get(event_id).photos.get(photo_id);
      photo.count_comments++;
      let comm_id = commRes.content.id;
      let newComment = Comments.constructFromJSON(commRes.content);
      photo.comments.set(comm_id, newComment);
      alert('Comment added successfully!');
    });
  }

  @action
  deleteEventWithSync (event_id) {
     services.deleteEvent(event_id, {}).then(eventRes => {
      if (eventRes.error == true) {alert('Unable to delete event detail');return;}
      if (this.appStatus.event_id === event_id) {
        this.setSelectedEvent('');
        this.appStatus.isCreateEvent = true;
        this.appStatus.eventSearchbar = true;
        this.appStatus.editEvent = false;
        this.initTempEvent();
      }
      this.events.delete(event_id);
      alert('Event detail deleted successfully!');
     });
  }

  @action
  updateEventWithSync (file) {
    const formData = new FormData();
    let event_id = this.tempEvent.id;
    formData.append('name', this.tempEvent.name);
    if (file.name) {
      formData.append('hero_image', file, file.name);
    }

    formData.append('description', this.tempEvent.description);
    formData.append('location', "");
    formData.append('bypass_moderation', this.tempEvent.bypass_moderation);

    services.updateEvent(event_id, formData).then(eventRes => {
      if (eventRes.error == true) {
        this.appStatus.processing = false;
        alert('Unable to update event detail');
        return;
      }
      this.tempEvent.update({hero_image:eventRes.content.hero_image});
      this.events.set(event_id, this.tempEvent);
      this.initUpdateTempEvent(this.tempEvent);
      this.appStatus.processing = false;
      this.setSelectedEvent(event_id);
      this.getPhotos(event_id);
      this.appStatus.isCreateEvent = false;
      this.appStatus.isCreateEventContent = true;
      this.appStatus.photo_id = '';
      alert('Event details updated successfully!');
    });
  }

  @action 
  createEventWithSync() {
    const formData = new FormData();
    formData.append('name', this.tempEvent.name);
    formData.append('description', this.tempEvent.description);
    formData.append('hero_image', this.tempEvent.hero_image, this.tempEvent.hero_image.name);
    formData.append('location', "");
    formData.append('bypass_moderation', this.tempEvent.bypass_moderation);

    services.createEvent(formData).then(eventRes => {
      if (eventRes.error == true) {
        this.appStatus.processing = false;
        alert('Unable to create event detail');
        return;
      }
      let event_id = eventRes.content.id;
      let newEvent = Event.constructFromJSON(eventRes.content);
      this.events.set(event_id, newEvent);
      this.appStatus.isCreateEventContent = false;
      this.setSelectedEvent(event_id);
      this.appStatus.isCreateEvent = false;
      this.appStatus.processing = false;
      this.initTempEventContents(eventRes.content);
      alert("Event created successfully!");
    });
  }

  @action 
  createEventContentWithSync() {
    let event = this.events.get(this.appStatus.event_id);
    const formData = new FormData();
    let temp = this.tempEventContent;
    formData.append('description', this.tempEventContent.description);
    formData.append('photo', temp.user_profile_image, temp.user_profile_image.name);
    formData.append('location', this.geo_loc);
    services.createEventContent(event.id, formData).then(eventContRes => {
      if (eventContRes.error == true) {
        this.appStatus.processing = false;
        alert('Unable to create event content');
        return;
      }
      services.getEventById(event.id).then(eveRes => {
        let temp_event = Event.constructFromJSON(eveRes.content);
        let photo_id = eventContRes.content.id;
        let newEventCont = Photos.constructFromJSON(eventContRes.content);
        event.photos.set(photo_id, newEventCont);
        temp_event.photos = event.photos;
        this.events.set(this.appStatus.event_id, temp_event);
        this.appStatus.processing = false;
        this.appStatus.photo_id = photo_id;
        alert("Event content added successfully!");
      });
    });
  }

  @action
  initTempEvent() {
    this.tempEvent = new Event('', '','','','','', '','','','', [],'',false)
    return this.tempEvent;
  }

  @action
  initUpdateTempEvent(json) {
    this.tempEvent = Event.constructFromJSON(json);
    return this.tempEvent;
  }

  @action
  initTempEventContent() {
    this.tempEventContent = new Photos('','','','','','','','','','','','');
    return this.tempEventContent;
  }

  @action
  initTempEventContents(json) {
    this.tempEventContent = new Photos(
      json.id,
      json.user_id,
      json.user_name,
      json.user_profile_image,
      json.file_url,
      json.description,
      json.comments ? json.comments.values():[],
      json.count_likes,
      json.created_at,
      json.flagged,
      json.moderated,
      json.user_has_liked,
      json.count_comments);
    return this.tempEventContent;
  }

  @action
  initTempComment(json) {
    this.comment = Comments.constructFromJSON(json);
    return this.comment;
  }  

}

const store = new Store;

export default store;


