/*
** This component is used for to show comment details
*/

import React from 'react';
import { inject, observer } from 'mobx-react';
import moment from 'moment';

@inject('store')
@observer
export default class Comments extends React.Component {
 
  // default constructor
  constructor() {
    super();
    this.state = {isDeleted: false}
  }

  // default properties
  static propTypes = {
    comment_id: React.PropTypes.number.isRequired
  }

  // edit comment of selected event content
  editComment (event) {
    let { appStatus } = this.props.store;
    appStatus.comment = this.props.comment;
    appStatus.editComment = true;
    appStatus.editCommLoading = false;
    appStatus.comment_id = this.props.comment_id;
    this.props.store.initTempComment(this.props.comment);
    event.preventDefault();
  }

  // Delete comment of selected event content
  deleteComment (event) {
    let t = confirm('Are you sure you want to delete it?');
    if (!t) return;
    let comment_id = this.props.comment_id;
    this.props.store.deleteComment(comment_id);
    this.setState({isDeleted: true});
    event.preventDefault();
  }

  render() {
    let comment_id = this.props.comment_id;
    let comment = this.props.comment;
    if (this.state.isDeleted == true) {
      return (<div> </div>);
    }
    let { appStatus } = this.props.store;
    let created_at = moment(comment.created_at.slice(0, -4), "DD-MM-YYYY  hh:mm:ss a").format('LLL');

    return (
      <div className="view-row">
        <div className="view-detail">
          <div className="pull-left">
            <span className="user_pic">
              {comment.user_profile_image ? 
                  <img src={comment.user_profile_image} className="img-responsive" />
                  :
                  <img src="./../user_profile_image.png" className="img-responsive" />
              }
            </span> 
            <span><b>{comment.user_name}</b></span>
          </div>
          <div className="pull-right"><a className="edit-icon" onClick={this.editComment.bind(this)}><span className=" bluecolor fa fa-pencil"></span> </a><a  className="edit-icon" onClick={this.deleteComment.bind(this)}>
              <i className="fa fa-times" aria-hidden="true"></i>
            </a></div>

            <div className="clearfix"></div>
            <div className="block comment_box"><span></span><span>{comment.text}</span>
                   <div className="clearfix"></div>
            
            </div>
            <p className=" pull-right comment_date"><span className="font_small">{created_at}</span></p>
          <div className="clearfix"></div>          
        </div>
      </div>
    );
  }
}


