/*
** This component is used for to show the event content header
*/

import React from 'react';
import { inject, observer } from 'mobx-react';

import {
  ShareButtons,
  ShareCounts,
  generateShareIcon,
} from 'react-share';


const {
  FacebookShareButton,
  TwitterShareButton,
} = ShareButtons;

const FacebookIcon = generateShareIcon('facebook');
const TwitterIcon = generateShareIcon('twitter');

@inject('store')
@observer
export default class EventContainerHead extends React.Component {
 
  constructor() {
    super();
    this.state = {flagged: false}
  }

  componentWillMount() {
    let { store } = this.props;
    let { events, appStatus } = this.props.store;
    let photo_id =  this.props.photo_id;
    let photo = events.get(appStatus.event_id).photos.get(photo_id);
    store.initTempEventContents(photo);
  }

  flagEventContent (e) {
    let { store } = this.props;
    let flag = this.props.photo.flagged ? false : true;
    let photo_id =  this.props.photo_id;
    let photo = store.events.get(store.appStatus.event_id).photos.get(photo_id);
    store.initTempEventContents(photo);
    let tempEventContent = store.tempEventContent;
    tempEventContent.update({flagged:flag});
    store.flagEventContent(flag, photo_id, tempEventContent);
    e.preventDefault();
  }

  deleteEventContent (event) {
    let t = confirm('Are you sure you want to delete it?');
    if (!t) return;
    let { store } = this.props;
    let photo_id =  this.props.photo_id;
    store.appStatus.isCreateEventContent = true;
    store.appStatus.photo_id = '';
    store.deleteEventContent(photo_id);
    event.preventDefault();
  }

  selectedEventContiner (event) {
    let { appStatus, events } = this.props.store;
    if( appStatus.photo_id == this.props.photo_id) {
      appStatus.photo_id = '';
    } else {
      appStatus.photo_id = this.props.photo_id;
      let photo = events.get(appStatus.event_id).photos.get(appStatus.photo_id);
      appStatus.editComment = false;
      this.props.store.getPhotosComments(appStatus.event_id, appStatus.photo_id);
      this.props.store.initTempEventContents(photo);
    }

    event.preventDefault();
  }

  handleDownloadPhotos (event) {
    let { events, appStatus } = this.props.store;
    let photo = events.get(appStatus.event_id).photos.get(this.props.photo_id);
    window.open(photo.file_url, 'sharer', 'toolbar=0,status=0,width=548,height=325');
    event.preventDefault();
  }

  render() {
    let { events, appStatus, tempEventContent } = this.props.store;
    let uncheck = this.props.uncheck;
    let photo_id =  this.props.photo_id;
    let photo = events.get(appStatus.event_id).photos.get(photo_id);
    let pic = photo.file_url;
    let mime = '';
    if (pic)  {
      mime = pic.split('.').pop();
    }
    let show_check = (uncheck == 'true') && (appStatus.photo_id === photo_id );

    return (
      <div className="sub-header">
        <div className="camera float-left">
          <span><i className={mime == 'mp4' ? "fa fa-video-camera" : "fa fa-camera"}></i></span>
          {uncheck == 'true' ?  
            ( show_check ? 
              <span onClick={this.selectedEventContiner.bind(this)}>
                <i className="fa fa-check-square" aria-hidden="true"></i>
              </span>
              : 
              <span onClick={this.selectedEventContiner.bind(this)}>
                <i className="fa fa-square-o" aria-hidden="true"></i>
              </span>
            )
            : ''
          }
        </div>
        <div className="icon-group float-right">
          <span className="float-right" onClick={this.deleteEventContent.bind(this)}>
            <i className="fa fa-trash"></i>
          </span>
          <div className="dropdown share-it float-right">
            <button className="dropdown-toggle" type="button" data-toggle="dropdown">
            <i className="fa fa-share-alt-square"></i></button>
            <ul className="dropdown-menu">
              <li>
                <a onClick={this.handleDownloadPhotos.bind(this)}>
                  <i class="fa fa-download" aria-hidden="true"></i> Download
                </a>
              </li>
              <li>
                <a>
                <FacebookShareButton
                  url={photo.file_url}
                  className="Demo__some-network__share-button">
                  <span className="fa fa-facebook-official" aria-hidden="true"></span> Facebook
                </FacebookShareButton>
                </a>
              </li>
              <li>
                <a>
                <TwitterShareButton
                  url={photo.file_url}
                  className="Demo__some-network__share-button">
                  <span className="fa fa-twitter-square" aria-hidden="true"></span> Twitter
                </TwitterShareButton>
                </a>
              </li>
            </ul>
          </div>
          <span className="float-right" onClick={this.flagEventContent.bind(this)}>
            {photo.flagged === true ? <i className="fa fa-flag color-red"></i> : <i className="fa fa-sm fa-flag-o"></i>}
          </span>
        </div>
      </div>
    );
  }
  
} 


