/*
** This component is used for to show the event details
*/

import React from 'react';
import { inject, observer } from 'mobx-react';
import ImageShow from './ImageShow';
import moment from 'moment';
@inject('store')
@observer
export default class Event extends React.Component {

  constructor() {
    super();
    this.state = {imgLoader: false, timeInterval: 5000}
  }

  static propTypes = {
    event_id: React.PropTypes.number.isRequired
  }

  componentWillMount() {
    this.props.store.event_id = '';
  }

  handleDeleteEvent (event) {
    let t = confirm('Are you sure you want to delete it?');
    if (!t) return;
    let { store, event_id } = this.props;
    store.deleteEventWithSync(event_id);
    event.preventDefault();
  }

  handleEditEvent (e) {
    let { store, event_id } = this.props;
    store.appStatus.editEvent = true;
    store.appStatus.isCreateEvent = true;
    store.appStatus.eventSearchbar = true;
    this.props.store.setSelectedEvent(event_id);
    let event = store.events.get(event_id);
    store.initUpdateTempEvent(event);
    e.preventDefault();
  }

  selectEvent(e) {
    let { event_id, store } = this.props;
    store.appStatus.isCreateEventContent = false;
    if (store.getSelectedEvent()) {
      clearInterval(store.autoload);
    }
    if (store.getSelectedEvent() == event_id) {
      store.setSelectedEvent();
      store.appStatus.isCreateEvent = true;
    }
    else {
      store.setSelectedEvent(event_id);
      store.appStatus.isCreateEvent = false;
      store.appStatus.isCreateEventContent = true;
      store.appStatus.photo_id = '';
      store.getPhotos(event_id);
      let loader = setInterval(function() {
        store.getPhotos(event_id);
        let photo_id = store.appStatus.photo_id;
        /*if(photo_id) {
          store.getPhotosComments(event_id, store.appStatus.photo_id);
        }*/
      }, this.state.timeInterval);
      store.autoload = loader;
    }
    store.appStatus.isSearchContent = false;
    store.appStatus.searchContent = '';
    e.preventDefault();
  }


  render() {
    let { event_id, store } = this.props;
    let event = store.events.get(event_id);
    let activeClass = (event.id == store.appStatus.event_id) ? 'image-block active' : 'image-block';
    let block = [];
    let img_element = '';
    if ((event.id == store.appStatus.event_id)) {
      let img_url = event.hero_image ? event.hero_image : 'no-image.jpg';
      block.push(<ImageShow  key={0} img_src={img_url} thumb_url="" />);
      block.push(<div key={1} className="desciption align-center"><strong>{event.description}</strong></div>);
    }
    let color_status = 'bg-color-' + event.status_color;
    let created_at = moment(event.created_at, 'YYYY-MM-DD').format('LL');
    return (
      <div className={activeClass}>
        <div className="sub-header">
          <div className="title-block" onClick={this.selectEvent.bind(this)}>{event.name}</div>
          <div className="date-block">{created_at}</div>
          <div className="color-block">
            <div className={'color '+ color_status}></div>
            <div className="dropdown-arrow">
              <div className="dropdown">
                <button className="dropdown-toggle" type="button" data-toggle="dropdown">
                <span className="caret"></span></button>
                <ul className="dropdown-menu">
                  <li><a onClick={this.handleDeleteEvent.bind(this)}>Delete</a></li>
                  <li><a onClick={this.handleEditEvent.bind(this)}>Edit</a></li>
                </ul>
              </div>
            </div>
          </div>
        </div>
        {block}
      </div>
    );
  }
}


