/*
** This component is used for create events
*/

import React from 'react';
import { inject, observer } from 'mobx-react';

@inject('store')
@observer
export default class CreateEvent extends React.Component {
 
  constructor() {
    super();
    this.state = {isChecked: false}
  }

  handleCheckboxChange (event) {
    this.setState({isChecked: event.target.checked});
    this.props.store.tempEvent.update({bypass_moderation:event.target.checked});
  }

  handleCreateEventClick(event) {
    let { store } = this.props;
    if (!store.tempEvent.name.trim()) {
      store.tempEvent.update({name:''});
      alert('Please enter event name');
    } else if (!store.tempEvent.description.trim()) {
      store.tempEvent.update({description:''});
      alert('Please enter event description');
    } else if (!store.tempEvent.hero_image || !store.tempEvent.hero_image.name) {
      alert('Please select event image');
    } else {
      store.appStatus.processing = true;
      store.createEventWithSync();
    }
    event.preventDefault();
  }

  handleInputChange(event) {
    let fieldName = event.target.name;
    let fieldValue = event.target.value;
    let entries = {};
    entries[fieldName] = fieldValue;
    this.props.store.tempEvent.update(entries);
  }
  
  handleImageChange(e) {
    let file = e.target.files[0];
    if (file.type.includes('image/')) {
      this.props.store.tempEvent.update({hero_image: file});
    } else {
      e.target.value = '';
      alert('Please select image file');
    }
  }

  renderInputField(fieldName, fieldLabel, placeh) {
    return (
      <div className="form-group">
        <label className="h3 control-label" for="event-title">{fieldLabel}</label>
        <input 
          type="text"
          name={fieldName}
          className="form-control"
          placeholder={placeh}
          onChange={this.handleInputChange.bind(this)}
        />
      </div>
    );
  }

  renderTextareaField(fieldName, fieldLabel, placeh) {
    return (
      <div className="form-group">
        <label className="h3" for="event-description">{fieldLabel}</label>
        <textarea
          className="form-control"
          name={fieldName}
          id="description"
          rows="3"
          placeholder={placeh}
          maxLength="140"
          onChange={this.handleInputChange.bind(this)}
        />
      </div>
    );
  }

  renderFileField(fieldName, fieldLabel) {
    return (
      <div className="form-group">
        <label className="h3" for="event-description">{fieldLabel}</label>
        <input
          id="hero_image"
          name={fieldName}
          type="file"
          className="form-control-file"
          accept="image/*"
          onChange={this.handleImageChange.bind(this)}
        />
      </div>
    );
  }

  render() {
    let { processing } = this.props.store.appStatus;
    return (
      <div className="top-h-panel panel-h-right col-md-8">
        <div className="row event-container">
          <div className="panel panel-primary">
            <div className="panel-heading">
              <div className="panel-title">Create Event</div>
            </div>
            <div className="panel-body">
              <form onSubmit={this.handleCreateEventClick.bind(this)} encType="multipart/form-data">
                {this.renderInputField('name', 'Title', 'Enter the event title')}
                {this.renderTextareaField('description', 'Description', 'Enter the event description')}
                {this.renderFileField('hero_image', 'Photo')}

                <div className="form-group">
                  <label for="event-bypass">Bypass Moderation</label>&nbsp;&nbsp;&nbsp;
                  <input
                    type="checkbox"
                    className="form-check-input"
                    onChange={this.handleCheckboxChange.bind(this)} checked={this.state.isChecked}
                  />
                </div>
                <button 
                  type="submit"
                  className="btn btn-primary btn-100"
                  disabled={processing ? 'disabled' : ''}>
                    {processing ? 'Please wait' : 'Save'}
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}


