/*
** This component is used event header layout to show the event header details
**/

import React from 'react';
import { inject, observer } from 'mobx-react';

import { EventCountHead, CreateEventHead, EventSearchHead, EventContentHead, EventContentSearchHead } from '../components/';

@inject('store')
@observer
export default class EventComponent extends React.Component{

  constructor() {
    super();
  }

  render() {
    let { store } = this.props;
    let slot = [];
    let index = 0;
    if(store.appStatus.isCreateEvent === true) {
      slot.push(<CreateEventHead key={index++} />);
    } else if(store.getSelectedEvent()) {
      slot.push(<EventContentHead key={index++} />);
    }  else {
      slot.push(<div key={index++} className="col-md-4 event-count"></div>);
    }
    if(store.appStatus.eventSearchbar === true) {
      if (store.getSelectedEvent()) {
        slot.push(<EventContentSearchHead key={index++} />);
      } else {
        slot.push(<EventSearchHead key={index++} />);
      }
      
    } else {
      slot.push(<div key={index++} className="top-h-panel panel-h-right col-md-4"></div>);
    }

    return (
      <div className="row event-block">
        <EventCountHead />
        {slot}
      </div>
    );
  }
}



