/*
** This component is used for to edit event details
*/

import React from 'react';
import { inject, observer } from 'mobx-react';

@inject('store')
@observer
export default class EditEvent extends React.Component {
 
  constructor() {
    super();
    this.state = {
      name: '',
      description: '',
      bypass_moderation: false,
      hero_image: '',
    }
  }

  handleCheckboxChange (event) {
    this.setState({bypass_moderation: event.target.checked});
    this.props.store.tempEvent.update({bypass_moderation:event.target.checked});
  }

  handleUpdateEventClick(event) {
    let { store } = this.props;
    store.appStatus.processing = true;
    store.updateEventWithSync(this.state.hero_image);
    event.preventDefault();
  }

  handleInputChange(event) {
    let fieldName = event.target.name;
    let fieldValue = event.target.value;
    let entries = {};
    entries[fieldName] = fieldValue;
    this.setState(entries);
    this.props.store.tempEvent.update(entries);
  }
  
  handleImageChange(e) {
    let file = e.target.files[0];
    if (file.type.includes('image/')) {
      this.setState({hero_image:file});
    } else {
      e.target.value = '';
      alert('Please select image file');
    }
  }

  renderInputField(fieldName, fieldLabel, placeh, value) {
    return (
      <div className="form-group">
        <label className="h3 control-label" for="event-title">{fieldLabel}</label>
        <input 
          type="text"
          name={fieldName}
          className="form-control"
          placeholder={placeh}
          value={value}
          onChange={this.handleInputChange.bind(this)}
        />
      </div>
    );
  }

  renderTextareaField(fieldName, fieldLabel, placeh, value) {
    return (
      <div className="form-group">
        <label className="h3" for="event-description">{fieldLabel}</label>
        <textarea
          className="form-control"
          name={fieldName}
          id="description"
          rows="3"
          placeholder={placeh}
          onChange={this.handleInputChange.bind(this)}
          value={value}
        />
      </div>
    );
  }

  renderFileField(fieldName, fieldLabel) {
    return (
      <div className="form-group">
        <label className="h3" for="event-description">{fieldLabel}</label>
        <input
          id="hero_image"
          name={fieldName}
          type="file"
          className="form-control-file"
          accept="image/*"
          onChange={this.handleImageChange.bind(this)}
        />
      </div>
    );
  }

  render() {
    let { tempEvent, appStatus } = this.props.store;
    let processing = appStatus.processing;
    this.state.name = tempEvent.name ? tempEvent.name : '';
    this.state.description = tempEvent.description ? tempEvent.description : '';
    this.state.bypass_moderation = tempEvent.bypass_moderation ? tempEvent.bypass_moderation : false;

    return (
      <div className="top-h-panel panel-h-right col-md-8">
        <div className="row event-container">
          <div className="panel panel-primary">
            <div className="panel-heading">
              <div className="panel-title">Update Event</div>
            </div>
            <div className="panel-body">
              <form onSubmit={this.handleUpdateEventClick.bind(this)} encType="multipart/form-data">
                {this.renderInputField('name', 'Title', 'Enter the event title', this.state.name)}
                {this.renderTextareaField('description', 'Description', 'Enter the event description', this.state.description)}
                {this.renderFileField('hero_image', 'Photo')}
                <div className="form-group">
                  <label for="event-bypass">Bypass Moderation</label>&nbsp;&nbsp;&nbsp;
                  <input
                    type="checkbox"
                    className="form-check-input"
                    onChange={this.handleCheckboxChange.bind(this)} checked={this.state.bypass_moderation}
                  />
                </div>
                <button 
                  type="submit"
                  className="btn btn-primary btn-100"
                  disabled={processing ? 'disabled' : ''}>
                    {processing ? 'Please wait' : 'Save'}
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}


