/*
** This component is used for to show comments on selected event content
*/

import React from 'react';
import { inject, observer } from 'mobx-react';

import Comments from './Comments';

@inject('store')
@observer
export default class CommentsList extends React.Component {

  eventContComm (items) {
    if (items.length == 0) {return <div className="view-row"><div>No Comments</div></div>;}
    return items.map((el) => <Comments key={el.id} comment_id={el.id} comment={el} />);
  }

  render() {
    let commentHTML = this.eventContComm(this.props.comments);
    let css_styles = this.props.css_styles;
    return (
      <div className="view-all" style={css_styles}>{commentHTML}</div>
    );
  }
}


