/*
** This file is used to create proto type of comment data
**/

import { action, computed, observable } from 'mobx';

export default class {
  id = null;
  @observable created_at = '';
  @observable flagged = false;
  @observable photo_id = id;
  @observable text = '';
  @observable user_id = '';
  @observable user_name = '';
  @observable user_profile_image = '';

  static jsonToArray(json) {
    let comment = {};
    json.map(item => {
      comment[item.id] = this.constructFromJSON(item);
    });
    return comment;
  }

  static constructFromJSON(json) {
    return new this(
      json.id,
      json.created_at,
      json.flagged,
      json.photo_id,
      json.text,
      json.user_id,
      json.user_name,
      json.user_profile_image
    );
  }    

  constructor(id,created_at,flagged,photo_id,text,user_id,user_name,user_profile_image) {
    this.id = id,
    this.created_at = created_at,
    this.flagged = flagged,
    this.photo_id = photo_id,
    this.text = text,
    this.user_id = user_id,
    this.user_name = user_name,
    this.user_profile_image = user_profile_image
  }

  @action
  update(entries) {
    for (let key in entries) {
      this[key] = entries[key];
    }
  }
}


