import axios from 'axios';

// List of API constants
const serviceURIs = {
  USERS: '/users',
  LOGOUT: '/session/logout',
  ADMINEVENTS: '/admin/galleries',
  EVENTS: '/galleries',
  COMMENT: '/admin/comments/',
  PHOTOS: '/admin/photos/',
  FLAGPHOTOS: '/photos/',
  APPVERSION: '/version'
}


// API post method
function postData(url, payroll) {
  return (
    axios.post(url, payroll).then(response => {
      return {error: false, content: response.data};
    }).catch(error => {
      return {error: true, content: {}};
    })
  );
}

// API get method
function getData(url) {
  return (
    axios.get(url).then(response => {
      return {error: false, content: response.data};
    }).catch(error => {
      return {error: true, content: {}};
    })
  )
}

// API patch method
function patchData(url, payload) {
  payload['_method'] = 'PUT';
  return (
    axios.put(url, payload).then(response => {
      return {error: false, content: response.data};
    }).catch(error => {
      return {error: true, content: {}};
    })
  );
}

// API delete method
function deleteData (url) {
  return (
    axios.delete(url).then(response => {
      return {error: false, content: response.data};
    }).catch(error => {
      return {error: true, content: {}};
    })
  );
}

// API delete event
function deleteEvent (event_id) {
  var theUrl = serviceURIs.ADMINEVENTS + '/' + event_id;
  return deleteData(theUrl);
}

// API delete flag event content
function deleteFlagEventContent (photo_id) {
  var theUrl = serviceURIs.PHOTOS + photo_id + '/flag';
  return deleteData(theUrl);
}

// API delete event content
function deleteEventContent (photo_id) {
  var theUrl = serviceURIs.PHOTOS + photo_id;
  return deleteData(theUrl);
}

// API delete comment
function deleteComment (comment_id) {
  var theUrl = serviceURIs.COMMENT + comment_id;
  return deleteData(theUrl);
}

// API get event photos
function getPhotos (event_id) {
  var theUrl = serviceURIs.ADMINEVENTS + '/' + event_id + '/feed?remove_comments';
  return getData(theUrl);
}

// API get event photos with comments
function getPhotoComments (event_id) {
  var theUrl = serviceURIs.ADMINEVENTS + '/' + event_id + '/feed';
  return getData(theUrl);
}

// API updte comment
function updateComment (comment_id, payroll) {
  var theUrl = serviceURIs.COMMENT + comment_id;
  return patchData(theUrl, payroll);
}

// API to create comments
function postComment (event_id, photo_id, payroll) {
  var theUrl = serviceURIs.EVENTS + '/' + event_id + '/photos/' + photo_id + '/comments';
  return postData(theUrl, payroll);
}

// API flag event content
function flagEventContent (photo_id, payroll) {
  var theUrl = serviceURIs.FLAGPHOTOS + photo_id + '/flag';
  return postData(theUrl, payroll);
}

// API create event content flag
function createEventContentFlaged (event_id, payroll) {
  var theUrl = serviceURIs.EVENTS + '/' + event_id + '/photos';
  return postData(theUrl, payroll);
}

// API create event content
function createEventContent(event_id, payroll) {
  var theUrl = serviceURIs.EVENTS + '/' + event_id + '/photos';
  return postData(theUrl, payroll);
}

// API update event detials
function updateEvent(event_id, payroll) {
  var theUrl = serviceURIs.EVENTS + '/' + event_id;
  return patchData(theUrl, payroll);
}

// API to create event
function createEvent(payroll) {
  var theUrl = serviceURIs.EVENTS;
  return postData(theUrl, payroll);
}

// API to get events
function getEvents () {
  var theUrl = serviceURIs.ADMINEVENTS+'?remove_photos';
  return getData(theUrl);
}

// API to get event by ID
function getEventById (event_id) {
  var theUrl = serviceURIs.ADMINEVENTS+'/'+event_id;
  return getData(theUrl);
}

// API for user logout
function logout () {
  var theUrl = serviceURIs.LOGOUT;
  return getData(theUrl);
}

// API to get user data
function gerUser (userID) {
	var theUrl = serviceURIs.USERS + '/'+ userID;
  return getData(theUrl);
}

// API to filter event content
function getFilteedEventContent (event_id, text) {
  var theUrl = serviceURIs.EVENTS + '/' + event_id + '/search?search=' + text;
  return getData(theUrl);
}


// API to filter event content
function getAppVersion () {
  var theUrl = serviceURIs.APPVERSION;
  return getData(theUrl);
}

// Export all method
export default {
  gerUser,
  logout,
  getEvents,
  createEvent,
  updateEvent,
  createEventContent,
  postComment,
  getPhotos,
  deleteComment,
  getPhotoComments,
  updateComment,
  deleteEventContent,
  deleteFlagEventContent,
  flagEventContent,
  getEventById,
  deleteEvent,
  getFilteedEventContent,
  getAppVersion
}


