/*
** This component is used for to show event details
*/

import React from 'react';
import { inject, observer } from 'mobx-react';

@inject('store')
@observer
export default class EventViewEditComponent extends React.Component {

  render() {
    let { appStatus, events } = this.props.store;
    let event = events.get(appStatus.event_id);

    return (
      <div className="view-edit-event">
        <div className="sub-title">EVENT DETAILS</div>
        <div className="panel panel-view panel-primary">
          <div className="panel-body view-edit-event">
            <p><b>Title</b> : {event.name}</p>
            <p><b>Description</b> : {event.description}</p>
            <p><b>Bypass Moderation</b> : {event.bypass_moderation == true ? <span><span className="btn btn-success"></span> Active</span> : <span><span className="btn btn-danger"></span> Inactive</span> }</p>
          </div>
        </div>
      </div>
    );
  }
}


