/*
** This file is used to show the number of event available in event list
**/

import React from 'react';
import { inject, observer } from 'mobx-react';

@inject('store')
@observer
export default class EventCount extends React.Component {

  constructor() {
    super();
  }

  componentWillMount() {
    this.props.store.initTempEvent();
  }

  newCreateEvent (event) {
    event.preventDefault();
    let { store } = this.props;
    store.setSelectedEvent('');
    store.appStatus.isCreateEvent = true;
    store.appStatus.eventSearchbar = true;
    store.appStatus.editEvent = false;
    store.initTempEvent();
  }

  render() {
    let { store } = this.props;
    let events = store.events;
    let isSearch = store.appStatus.isSearch;
    let filterEvents = store.filterEvents;
    return (
      <div className="col-md-4 event-count">
        <h3>EVENTS({isSearch == true ? filterEvents.size : events.size})</h3>
        <div className="btn-block">
          	<a onClick={this.newCreateEvent.bind(this)}>
              <span className="plus-icon">
                <i className="fa fa-plus" aria-hidden="true"></i>
              </span>
            </a>
        </div>
      </div>
    );
  } 
}



