/*
** This component is used for to create event content in selected event list
*/

import React from 'react';
import { inject, observer } from 'mobx-react';

@inject('store')
@observer
export default class CreateEventContent extends React.Component {

  componentWillMount() {
    let { store } = this.props;
    store.initTempEventContent();
  }

  handleCreateEventContentClick(event) {
    let { store } = this.props;
    if (!store.tempEventContent.description.trim()) {
      alert('Please enter event content description');
      store.tempEventContent.update({description:''});
    } else if (!store.tempEventContent.user_profile_image || !store.tempEventContent.user_profile_image.name) {
      alert('Please select event content image/video file');
    }
    else {
      store.appStatus.processing = true;
      store.createEventContentWithSync();
    }
    event.preventDefault();
  }

  handleInputChange(event) {
    let { tempEventContent } = this.props.store;
    let fieldName = event.target.name;
    let fieldValue = event.target.value;
    let entries = {};
    entries[fieldName] = fieldValue;
    this.props.store.tempEventContent.update(entries);
  }
  
  handleImageChange(e) {
    let file = e.target.files[0];
    if (file.type.includes('image/') || file.type.includes('video/')) {
      let { tempEventContent } = this.props.store;
      this.props.store.tempEventContent.update({user_profile_image: file});
    } else {
      e.target.value = '';
      alert('Please select image/video file');
    }
  }


  renderTextareaField(fieldName, fieldLabel, placeh) {
    let { tempEventContent } = this.props.store;

    return (
      <div className="form-group">
        <label className="h3" for="event-description">{fieldLabel}</label>
        <textarea
          className="form-control"
          name={fieldName}
          id="description"
          rows="3"
          placeholder={placeh}
          maxLength="140"
          onChange={this.handleInputChange.bind(this)}
        />
      </div>
    );
  }

  renderFileField(fieldName, fieldLabel) {
    let { tempEventContent } = this.props.store;
    return (
      <div className="form-group">
        <label className="h3" for="post-image">{fieldLabel}</label>
        <input
          id="hero_image"
          name={fieldName}
          type="file"
          className="form-control-file"
          accept="video/*,image/*"
          onChange={this.handleImageChange.bind(this)}
        />
      </div>
    );
  }

  render() {
    let { processing } = this.props.store.appStatus;

    return (
      <div className="view-edit-event">
        <form onSubmit={this.handleCreateEventContentClick.bind(this)} encType="multipart/form-data">
          <div className="sub-title top-border">Create Content</div>
          <div className="view-edit-event">
            {this.renderTextareaField('description', 'Description', 'Enter the event description')}
            {this.renderFileField('user_profile_image', 'Image / Video')}
          </div>
          <button 
            type="submit"
            className="btn btn-primary btn-100"
            disabled={processing ? 'disabled' : ''}>
              {processing ? 'Please wait' : 'Save'}
          </button>
        </form>
      </div>
    );
  }
}


