/*
** This Component is used for to edit comments on selected event content
*/

import React from 'react';
import { inject, observer } from 'mobx-react';

@inject('store')
@observer
export default class EditComment extends React.Component {
 
  constructor() {
    super();
    this.state = {comment: ''}
  }

  updateComment (event) {
    let { store } = this.props;
    store.UpdateComment(store.comment);
    event.preventDefault();
  }

  handleCommentChange (event) {
    this.setState({comment: event.target.value});
    let { comment } = this.props.store;
    let fieldName = event.target.name;
    let fieldValue = event.target.value;
    let entries = {};
    entries[fieldName] = fieldValue;
    comment.update(entries);
    event.preventDefault();
  }

  render() {
    let { comment } = this.props.store;
    this.state.comment = comment.text;

    return (
      <form onSubmit={this.updateComment.bind(this)}>
        <div className="form-group">
          <label>Edit Comment</label>
          <textarea
            className="form-control" 
            name="text"
            value={this.state.comment}
            onChange={this.handleCommentChange.bind(this)}
            placeholder="Add a new comment"
          />
        </div>
        <button type="submit" className="btn btn-primary btn-100">Save</button>
      </form>
    );
  }
}


