/*
** This file is used for to show the event content count and route to moderate page
*/

import React from 'react';
import { inject, observer } from 'mobx-react';

@inject('store')
@observer
export default class EventContentHead extends React.Component {

  constructor() {
    super();
  }

  newCreateEventContent (event) {
    event.preventDefault();
    let { appStatus } = this.props.store;
    appStatus.isCreateEventContent = true;
    appStatus.photo_id = '';
  }

  render() {
    let { appStatus, events, filterEventContent } = this.props.store;
    let event = events.get(appStatus.event_id);
    let isSearchContent = appStatus.isSearchContent;
    
    return (
      <div className="col-md-4 event-count">
        <h3>CONTENT({isSearchContent == true ? filterEventContent.size : event.photos.size})</h3>
        <div className="btn-block">
          <a href={'moderation/' + event.id} className="btn btn-primary">MODERATE</a>
          <a onClick={this.newCreateEventContent.bind(this)}>
            <span className="plus-icon"><i className="fa fa-plus" aria-hidden="true"></i></span>
          </a>
        </div>
      </div>
    );
  } 
}



