/*
** This component is used to create layout design to show the container -1,2 and 3
**/

import React from 'react';
import { inject, observer } from 'mobx-react';

import { EventList, CreateEvent, EditEvent, EventContentListing, EventContentSelected, EventContentDetails } from '../components/';

@inject('store')
@observer
export default class EventsCreate extends React.Component{
  constructor() {
    super();
  }

  render() {
    let { appStatus } = this.props.store;
    let slot = [];
    let index = 0;
    if(appStatus.isCreateEvent == true) {
      if (appStatus.editEvent == true) {
        slot.push(<EditEvent key={index++} event_id={appStatus.event_id} />);
      } else {
        slot.push(<CreateEvent key={index++} />);
      }
    } else if(appStatus.event_id && appStatus.photo_id) {
      slot.push(<EventContentListing key={index++} />);
      slot.push(<EventContentSelected key={index++} />);
    } else if (appStatus.event_id && !appStatus.photo_id) {
      slot.push(<EventContentListing key={index++} />);
      slot.push(<EventContentDetails key={index++} />);
    } else {
      slot.push(<div key={index++} className="top-h-panel panel-h-middle col-md-8"></div>);
    }

    return (
      <div className="row">
        <div className="top-panel flex">
          <EventList />
          {slot}
        </div>
      </div>
    );
  }
}


