/*
** This component is used for show the event details and create event content component
*/

import React from 'react';
import { inject, observer } from 'mobx-react';

import { EventViewEditComponent, CreateEventContent } from './../../components/';

@inject('store')
@observer
export default class EventContentDetails extends React.Component {

  render() {
    let { appStatus } = this.props.store;
    let slot = [];
    if (appStatus.isCreateEventContent == true) {
      slot.push(<CreateEventContent key={0}/>);
    }

    return (
      <div className="top-h-panel panel-h-right col-md-4">
        <EventViewEditComponent />
        {slot}
      </div>
    );
  }
}



