/*
** Object type defined for photos
**/

import { action, computed, observable } from 'mobx';

import Comments from './Comments';

export default class {
  id = null;
  @observable user_id = 0;
  @observable user_name = '';
  @observable user_profile_image = '';
  @observable file_url = null;
  @observable description = '';
  @observable count_likes = 0;
  @observable created_at = '';
  @observable flagged = '';
  @observable moderated = null;
  @observable user_has_liked = null;
  @observable show_comments = false;
  @observable count_comments = 0;
  @observable location = '';
  @observable thumb_url = null;
  comments = observable.map({});

  static jsonToArray(json) {
    let photo = {};
    json.map(item => {
      photo[item.id] = this.constructFromJSON(item);
    });
    return photo;
  }

  static constructFromJSON(json) {
    return new this(
      json.id,
      json.user_id,
      json.user_name,
      json.user_profile_image,
      json.file_url,
      json.description,
      json.comments,
      json.count_likes,
      json.created_at,
      json.flagged,
      json.moderated,
      json.user_has_liked,
      json.count_comments,
      json.location,
      json.thumb_url
    );
  }    

  constructor(
    id,
    user_id,
    user_name,
    user_profile_image,
    file_url,
    description,
    comments,
    count_likes,
    created_at,
    flagged,
    moderated,
    user_has_liked,
    count_comments,
    location,
    thumb_url
    ) {
      this.id = id;
      this.user_id = user_id;
      this.user_name = user_name;
      this.user_profile_image = user_profile_image;
      this.file_url = file_url;
      this.description = description;
      comments ? this.comments.replace(Comments.jsonToArray(comments)) : this.comments;
      this.count_likes = count_likes ? count_likes : 0;
      this.created_at = created_at;
      this.flagged = flagged;
      this.moderated = moderated;
      this.user_has_liked = user_has_liked;
      this.show_comments = false;
      this.count_comments = count_comments ? count_comments : 0;
      this.location = location;
      this.thumb_url = thumb_url;
  }

  update(entries) {
    for (let key in entries) {
      this[key] = entries[key];
    }
  }

  updateShowMessage(status) {
    this.show_comments = status;
  }
}


